<template>
  <div class="main-container">
    <div class="table-container">
      <el-table :data="pageData.list" height="calc(100vh - 208px)">
        <el-table-column
          prop="seriesName"
          label="服务名"
          align="left"
        ></el-table-column>
        <el-table-column
          prop="agencyLevelName"
          label="代理级别"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="durationYear"
          label="期限（年）"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="orderNo"
          label="订单编号"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="createdTime"
          label="下单时间"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="price"
          label="价格"
          align="center"
        ></el-table-column>
        <el-table-column
          label="状态"
          align="center"
          fixed="right"
          class-name="operation-group"
        >
          <template #default="{ row }">
            <template v-if="row.paymentType !== 4">
              <el-link
                v-if="row.orderState === 1"
                @click="handlePaymentOrder(row)"
                type="primary"
                :underline="false"
                >去支付
              </el-link>
              <el-link
                v-if="row.orderState === 1"
                @click="handleCancelOrder(row)"
                type="danger"
                :underline="false"
                >取消订单
              </el-link>
            </template>
            <el-tag v-if="row.orderState === 1 && row.paymentType === 4"
              >正在审核</el-tag
            >
            <el-tag type="success" v-if="row.orderState === 2">已支付</el-tag>
            <el-tag type="danger" v-if="row.orderState === 3">已取消</el-tag>
          </template>
        </el-table-column>
        <template #empty>
          <TableEmpty empty-text="暂无订单"></TableEmpty>
        </template>
      </el-table>
      <!-- 分页 -->
      <PagePagination
        :pageTotal="pageData.pageTotal"
        :pageSize="pageData.pageSize"
        :pageNumber="pageData.pageNumber"
        @emitPageSizeChange="pageSizeChange"
        @emitPageNumberChange="pageNumberChange"
      ></PagePagination>
    </div>
  </div>
  <!--  支付二维码  -->
  <BuyQRCodeDialog
    v-model:visible="initData.paymentVisible"
    :paymentInfo="initData.paymentInfo"
  ></BuyQRCodeDialog>
</template>

<script lang="ts" setup>
import { ElMessage, ElMessageBox } from "element-plus";
import { shallowReactive, defineAsyncComponent } from "vue";

import { usePage } from "jz-tool-lib";
import { useVueRouter } from "@assets/useHook";

import { agentOrderApi, cancelOrderApi } from "@api/market-service-api";

const BuyQRCodeDialog = defineAsyncComponent(
  () => import("@components/payment/buy-QRCode-dialog.vue")
);

defineOptions({
  name: "marketService.personalOrder",
});

const { goPage } = useVueRouter();

const initData = shallowReactive({
  paymentVisible: false,
  paymentInfo: {},
});

const { getList, pageNumberChange, pageSizeChange, pageData, resetPageNumber } =
  usePage({
    listApi: agentOrderApi,
    props: {
      pageNumber: "current",
      pageSize: "size",
    },
  });

const handlePaymentOrder = (data) => {
  let modePayment = "";
  if ([0, 1].includes(data.paymentType)) {
    modePayment = "onlinePayment";
  } else if (data.paymentType === 3) {
    modePayment = "balancePayment";
  } else if (data.paymentType === 4) {
    modePayment = "offlinePayment";
  }
  if (data.orderType === 1) {
    goPage(
      `/payment/deposit?amount=${data.price}&mode=${data.mode}&paymentType=${data.paymentType}&modePayment=${modePayment}&orderNo=${data.orderNo}`
    );
  } else {
    goPage(
      `/payment/buy?productId=${data.seriesId}&amount=${data.price}&agencyLevel=${data.agencyLevel}&durationYear=${data.durationYear}&mode=${data.mode}&orderType=${data.orderType}&paymentType=${data.paymentType}&modePayment=${modePayment}&orderNo=${data.orderNo}`
    );
  }
};

// 取消订单
const handleCancelOrder = (row) => {
  ElMessageBox.confirm("是否确认取消支付该订单?", "提示", {
    confirmButtonText: "确定",
    cancelButtonText: "取消",
    type: "warning",
  }).then(() => {
    cancelOrderApi.getData(row.orderNo).then(() => {
      ElMessage.success("已取消");
      resetPageNumber();
      getList();
    });
  });
};
</script>
<style lang="scss" scoped>
.table-container .el-table {
  margin-top: 0;
}
</style>
